interface Size {
  width: number | undefined,
  height: number | undefined
}

interface FileMeta {
  name: string,
  id: string,
  mimeType: string
}

interface FileCardProps {
  thumbLink: string | null,
  fileName: string,
  size: Size
}

type FileGroup = Promise<FileCardProps>[];

const getFileParentIDs = async (fileId: string) => {
  const access_token = cookies().get('access_token')?.value;
  const req = new NextRequest(`https://www.googleapis.com/drive/v3/files/${fileId}?fields=*`, { headers: { Authorization: `Bearer ${access_token as string}`, alt: 'media' } });
  const file = await fetch(req).then(res => res.json()).catch(error => console.log(error));
  if(!file) { 
    console.error(new Error(`FILE NOT FOUND: fileId: ${fileId}`));
    return null;
  }
  // console.log("file.parents:", file?.parents[0] || file?.name);
  const parent = file?.parents ? file.parents[0] : null;
  return parent;
}

const getFiles = async () => {
  const access_token = cookies().get('access_token')?.value;
  const req = new NextRequest('https://www.googleapis.com/drive/v3/files', { headers: { Authorization: `Bearer ${access_token}` } });
  const res = await fetch(req).then(res => res.json()).then(json => json.files).then(files => files.sort((f1, f2) => f1.name < f2.name ? -1 : f1.name === f2.name ?  0 : 1));
  return res;
}

const getThumbnail: (fileId: string) => Promise<string> = async (fileId: string) => {
  const access_token = cookies().get('access_token')?.value;

  const req = new NextRequest(`https://www.googleapis.com/drive/v3/files/${fileId}?fields=thumbnailLink`, { headers: { Authorization: `Bearer ${access_token as string}` } });
  const file = await fetch(req).then(res => res.json()).catch(error => null);

  const url = file?.thumbnailLink ?? null;
  return url;

}

